import React, { forwardRef } from 'react';
import { string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-listbox-input';

const ListboxInput = forwardRef(({ className, type, ...remainingProps }, ref) => (
  <input
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    type={type}
    {...remainingProps}
  />
));

ListboxInput.propTypes = {
  /**
   * @ignore
   */
  className: string,

  /**
   * Type
   */
  type: string
};

ListboxInput.defaultProps = {
  type: 'hidden'
};

ListboxInput.displayName = 'ListboxInput';

export default ListboxInput;
