import React, { forwardRef } from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-listbox-label';

const ListboxLabel = forwardRef(({ children, className, ...remainingProps }, ref) => (
  <label
    className={classnames(CSS_PREFIX, className)}
    ref={ref}
    {...remainingProps}
  >
    {children}
  </label>
));

ListboxLabel.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

ListboxLabel.displayName = 'ListboxLabel';

export default ListboxLabel;
